import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'api-code',
    pathMatch: 'full'
  },
  {
    path: 'login',
    loadChildren: () => import('./login/login.module').then( m => m.LoginPageModule)
  },
  {
    path: 'profile',
    loadChildren: () => import('./profile/profile.module').then( m => m.ProfilePageModule)
  },
  {
    path: 'ticket',
    loadChildren: () => import('./ticket/ticket.module').then( m => m.TicketPageModule)
  },
  {
    path: 'ticket-detail',
    loadChildren: () => import('./ticket-detail/ticket-detail.module').then( m => m.TicketDetailPageModule)
  },
  {
    path: 'closer-detail',
    loadChildren: () => import('./closer-detail/closer-detail.module').then( m => m.CloserDetailPageModule)
  },
  {
    path: 'help',
    loadChildren: () => import('./help/help.module').then( m => m.HelpPageModule)
  },
  {
    path: 'service-form',
    loadChildren: () => import('./service-form/service-form.module').then( m => m.ServiceFormPageModule)
  },
  {
    path: 'support',
    loadChildren: () => import('./support/support.module').then( m => m.SupportPageModule)
  },
  {
    path: 'forgotpass',
    loadChildren: () => import('./forgotpass/forgotpass.module').then( m => m.ForgotpassPageModule)
  },
  {
    path: 'dashboard',
    loadChildren: () => import('./dashboard/dashboard.module').then( m => m.DashboardPageModule)
  },
  {
    path: 'view-closer',
    loadChildren: () => import('./view-closer/view-closer.module').then( m => m.ViewCloserPageModule)
  },
  {
    path: 'map',
    loadChildren: () => import('./map/map.module').then( m => m.MapPageModule)
  },
  {
    path: 'post-serviceform',
    loadChildren: () => import('./post-serviceform/post-serviceform.module').then( m => m.PostServiceformPageModule)
  },
  {
    path: 'api-code',
    loadChildren: () => import('./api-code/api-code.module').then( m => m.ApiCodePageModule)
  },
  {
    path: 'offlinelist',
    loadChildren: () => import('./offlinelist/offlinelist.module').then( m => m.OfflinelistPageModule)
  },
  {
    path: 'clienthistory',
    loadChildren: () => import('./clienthistory/clienthistory.module').then( m => m.ClienthistoryPageModule)
  },
  {
    path: 'cal-model',
    loadChildren: () => import('./cal-model/cal-model.module').then( m => m.CalModelPageModule)
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
