import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { LoadingController, AlertController } from '@ionic/angular';
import { NativeStorage } from '@ionic-native/native-storage/ngx';

@Injectable({
  providedIn: 'root'
})
export class RestService {
  //apiUrl = 'https://fsm.bthrust.com/api/';
  apiUrl = ''

  constructor(private http: HttpClient, public loadingController: LoadingController, public nativeStorage: NativeStorage, private alertController: AlertController) {

  }



  /*********For Login ************************************ */
  async loginApi(data) {

    if (localStorage.CompanyUrl) {
      this.apiUrl = localStorage.CompanyUrl + '/api/'
    } else {
      this.apiUrl = 'https://fsm.bthrust.com/api/'
    }

    const loading = await this.loadingController.create({
      message: 'Please wait...',
    });
    loading.present();
    // let  formData = {"UserName":"aiden@dummy.com","Password":"123456"}; //'username='+username+'&password='+password+'&usertype='+usertype+'';
    return new Promise((resolve, reject) => {
      let headers = new HttpHeaders({
        'Content-Type': ' application/json',
        'Accept': 'application/json'
      });
      var prefix;
      if (localStorage.CompanyUrl) {
        prefix = localStorage.CompanyUrl + '/'
      } else {
        prefix = 'https://fsm.bthrust.com/'
      }
      localStorage.setItem('urlPrefix', prefix);
      this.http.post(this.apiUrl + 'Logins/EngineerLogin', data, { headers: headers })
        .subscribe(res => {
          loading.dismiss();
          resolve(res);
        }, (err) => {
          loading.dismiss();
          reject(err);
        });

    });
  }

  async postServices(method, data) {
    if (localStorage.CompanyUrl) {
      this.apiUrl = localStorage.CompanyUrl + '/api/'
    } else {
      this.apiUrl = 'https://fsm.bthrust.com/api/'
    }

    const loading = await this.loadingController.create({
      message: 'Please wait...',
    });
    loading.present();
    return new Promise((resolve, reject) => {
      let headers = new HttpHeaders({
        'Content-Type': ' application/json',
        'Accept': 'application/json'
      });

      this.http.post(this.apiUrl + method, data, { headers: headers })
        .subscribe(res => {
          loading.dismiss();
          resolve(res);
        }, (err) => {
          loading.dismiss();
          reject(err);
        });
    });
  }

  async putServices(method, data) {
    if (localStorage.CompanyUrl) {
      this.apiUrl = localStorage.CompanyUrl + '/api/'
    } else {
      this.apiUrl = 'https://fsm.bthrust.com/api/'
    }

    const loading = await this.loadingController.create({
      message: 'Please wait...',
    });
    loading.present();
    return new Promise((resolve, reject) => {
      let headers = new HttpHeaders({
        'Content-Type': ' application/json',
        'Accept': 'application/json'
      });

      this.http.put(this.apiUrl + method, data, { headers: headers })
        .subscribe(res => {
          loading.dismiss();
          resolve(res);
        }, (err) => {
          loading.dismiss();
          reject(err);
        });
    });
  }

  async requestViaGet(actionName, params) {
    if (localStorage.CompanyUrl) {
      this.apiUrl = localStorage.CompanyUrl + '/api/'
    } else {
      this.apiUrl = 'https://fsm.bthrust.com/api/'
    }

    const loading = await this.loadingController.create({
      message: 'Please wait...',
    });
    loading.present();
    return new Promise((resolve, reject) => {
      let httpOptions = {
        headers: new HttpHeaders({
          'Content-Type': ' application/json',
          'Accept': 'application/json'
        })
      };
      httpOptions["params"] = params;
      this.http.get(this.apiUrl + actionName, httpOptions)
        .subscribe(res => {
          loading.dismiss();
          resolve(res);
        }, (err) => {
          loading.dismiss();
          reject(err);
        });
    });
  }

  async requestTimer(actionName, params) {
    if (localStorage.CompanyUrl) {
      this.apiUrl = localStorage.CompanyUrl + '/api/'
    } else {
      this.apiUrl = 'https://fsm.bthrust.com/api/'
    }
    return new Promise((resolve, reject) => {
      let httpOptions = {
        headers: new HttpHeaders({
          'Content-Type': ' application/json',
          'Accept': 'application/json'
        })
      };
      httpOptions["params"] = params;
      this.http.get(this.apiUrl + actionName, httpOptions)
        .subscribe(res => {
          resolve(res);
        }, (err) => {
          reject(err);
        });
    });
  }

  /***************** function for sync offline data in background without loader********************* */

  async postServicesOffline(method, data) {
    if (localStorage.CompanyUrl) {
      this.apiUrl = localStorage.CompanyUrl + '/api/'
    } else {
      this.apiUrl = 'https://fsm.bthrust.com/api/'
    }

    const loading = await this.loadingController.create({
      message: 'Please wait...',
    });
    // loading.present();
    return new Promise((resolve, reject) => {
      let headers = new HttpHeaders({
        'Content-Type': ' application/json',
        'Accept': 'application/json'
      });

      this.http.post(this.apiUrl + method, data, { headers: headers })
        .subscribe(res => {
          //  loading.dismiss();
          resolve(res);
        }, (err) => {
          //  loading.dismiss();
          reject(err);
        });
    });
  }

}
