import { Component, ViewChild } from '@angular/core';
import { Platform, IonRouterOutlet, NavController, AlertController, Events } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { Router } from '@angular/router';
import { NativeStorage } from '@ionic-native/native-storage/ngx';
import { Network } from '@ionic-native/network/ngx';
import { RestService } from './provider/rest.service';
//import { FCM } from '@ionic-native/fcm/ngx';
import { FCM } from "cordova-plugin-fcm-with-dependecy-updated/ionic/ngx";
import { Device } from '@ionic-native/device/ngx';
//import { BackgroundGeolocation, BackgroundGeolocationConfig, BackgroundGeolocationEvents, BackgroundGeolocationResponse } from '@ionic-native/background-geolocation/ngx';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss']
})
export class AppComponent {
  public appPages = [
    {
      title: 'Profile',
      url: '/profile',
      icon: 'contact'
    },
    // {
    //   title: 'Offline List',
    //   url: '/offlinelist',
    //   icon: 'contact'
    // },
    {
      title: 'Ticket',
      url: '/ticket',
      icon: 'cash'
    },
    {
      title: 'Help',
      url: '/help',
      icon: 'help-circle'
    }
  ];

  leftMenuData: any = [];
  urlPrefix: any;
  syncListData: any;
  response: any;
  offlinetokendata: any;
  Token: any;
  closerData: any;
  savedKeys: any = [];
  closerDataArr: any = [];
  assignedFormArr: any = [];
  prodServicArr: any = [];
  @ViewChild(IonRouterOutlet, { static: false }) routerOutlet: IonRouterOutlet;
  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    private navCtrl: NavController,
    public alertController: AlertController,
    private events: Events,
    private router: Router,
    private nativeStorage: NativeStorage,
    private network: Network,
    private RestService: RestService,
    private fcm: FCM,
    private device: Device,
    //private backgroundGeolocation: BackgroundGeolocation
  ) {
    this.initializeApp();
    events.subscribe('user:logged', (user) => {
      // console.log('Welcome', user);
      this.leftMenuData = user;
      this.urlPrefix = localStorage.urlPrefix
    });
  }

  initializeApp() {
    this.platform.ready().then(() => {
      this.statusBar.backgroundColorByHexString('#4771C7');
      this.splashScreen.hide();

      console.log('Device UUID is: ' + this.device.uuid);
      localStorage.setItem('deviceID', this.device.uuid);
      //================firebase token start===============================>

      this.fcm.getToken().then(token => {
        //backend.registerToken(token);
        localStorage.setItem('token', token);
        console.log(token);
        // th.sendDeviceToken(token);
      });
      this.fcm.onNotification().subscribe(data => {
        console.log(data);
        // setTimeout(() => {
        if (data.wasTapped) {
          console.log("Received in background");
          alert(JSON.stringify(data));

        } else {
          console.log("Received in foreground");
          if (this.platform.is('ios')) {
            alert(JSON.stringify(data));
          } else if (this.platform.is('android')) {
            alert(JSON.stringify(data));
          }
        };
      });

      this.fcm.onTokenRefresh().subscribe(token => {
        console.log(token);
        localStorage.setItem('token', token);
      });

      this.fcm.hasPermission().then(hasPermission => {
        if (hasPermission) {
          console.log("Has permission!");
        }
      })
      //================firebase token end===============================>

      //================location in background Start===============================>
      // const config: BackgroundGeolocationConfig = {
      //   desiredAccuracy: 10,
      //   stationaryRadius: 20,
      //   distanceFilter: 30,
      //   debug: true, //  enable this hear sounds for background-geolocation life-cycle.
      //   stopOnTerminate: false, // enable this to clear background location settings when the app terminates
      // };

      // this.backgroundGeolocation.configure(config)
      //   .then(() => {

      //     this.backgroundGeolocation.on(BackgroundGeolocationEvents.location).subscribe((location: BackgroundGeolocationResponse) => {
      //       console.log(location);
      //       alert(JSON.stringify(location));
      //       // IMPORTANT:  You must execute the finish method here to inform the native plugin that you're finished,
      //       // and the background-task may be completed.  You must do this regardless if your operations are successful or not.
      //       // IF YOU DON'T, ios will CRASH YOUR APP for spending too much time in the background.
      //       this.backgroundGeolocation.finish(); // FOR IOS ONLY
      //     });

      //   });

      // // start recording location
      // this.backgroundGeolocation.start();

      // If you wish to turn OFF background-tracking, call the #stop method.
      // this.backgroundGeolocation.stop();
      //================location in background end===============================>
    });


    if (localStorage.CompanyUrl) {
      if (localStorage.modeType == 'Offline' && localStorage.remember) {
        this.router.navigate(['dashboard']);
      } else {
        this.router.navigate(['login']);
      }
    } else {
      this.router.navigate(['api-code']);
    }

    this.platform.backButton.subscribeWithPriority(0, () => {
      if (this.routerOutlet && this.routerOutlet.canGoBack()) {
        this.routerOutlet.pop();
      } else if (this.router.url === '/dashboard') {
        navigator['app'].exitApp();
      } else {
        navigator['app'].exitApp();
      }
    });


    /***************** checking netowrk when connect to any network********************* */
    this.network.onConnect().subscribe(() => {
      console.log('network connected')
      if (localStorage.modeType == 'Offline') {
        this.Token = JSON.parse(localStorage.userData).Token;
        this.getKeys();
        this.getcloserArr();
        this.synchAssignedForm();
      }
    })

  }

  async logout() {
    const alert = await this.alertController.create({
      subHeader: 'Confirm Logout',
      message: 'Are you sure you want to log out?',
      cssClass: 'cstmAlert',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
          }
        },
        {
          text: 'Logout',
          handler: () => {
            localStorage.clear();
            this.nativeStorage.clear();
            this.navCtrl.navigateRoot("/api-code");
          }
        }
      ]
    });
    await alert.present();
  }

  /***************** offline sync work start********************* */


  /***************** product/service sync work start********************* */
  getKeys() {
    this.nativeStorage.keys()
      .then(
        data => {
          console.log(data)
          this.savedKeys = data;
          for (let i = 0; i < data.length; i++) {
            this.displaySyncData(data[i]);
          }
        },
        error => console.error(error)
      );
  }

  displaySyncData(key) {
    try {
      this.prodServicArr = [];
      this.nativeStorage.getItem(key)
        .then(
          data => {
            console.log(data)
            if (key.includes("Product") || key.includes("Service")) {
              this.prodServicArr.push(data);
              this.syncProduct(data)
            }
          },
          error => console.error(error)
        );
    } catch (e) {


    }
  }

  syncProduct(item) {
    this.RestService.postServicesOffline('app/EngineerTickets/PostTicketItem/' + this.Token, item).then((response) => {
      console.log(response);
      this.response = response;
      if (this.response.Message == "success") {
        this.successAlert();
        this.nativeStorage.remove(item.ItemName).then(
          data => {
            console.log(data)
          },
          error => console.error(error)
        );
        //  this.getKeys();
      } else {
      }
    });
  }

  /***************** product/service sync work end********************* */
  /***************** closer sync work start********************* */

  getcloserArr() {
    this.nativeStorage.getItem('closerArr')
      .then(
        data => {
          console.log(data)
          this.closerDataArr = data;
          for (let i = 0; i < data.length; i++) {
            this.syncData(data[i], i)
          }
        },
        error => console.error(error)
      );
  }

  syncData(data, i) {

    this.offlinetokendata = {
      "CloserID": "00000000-0000-0000-0000-000000000000",
      "TicketId": data.TicketId,
      "EngineerId": data.EngineerId,
    };

    this.RestService.postServicesOffline('app/EngineerTickets/PostCloserDetails/' + this.Token, this.offlinetokendata).then((response) => {
      console.log(response);
      this.response = response;
      if (this.response.Message == "success") {
        this.syncOfflineCloserData(data, response, i);
        this.offlineCloserDoc(data.TicketId, response);
      } else {

      }
    });
  }

  syncOfflineCloserData(data, resp, i) {
    //this.offlineCloserDoc(data.TicketId, resp);
    data.CloserID = resp.result.CloserID;
    data.CloserPrefix = resp.result.CloserPrefix

    this.RestService.postServicesOffline('app/EngineerTickets/OfflinePutCloserDetails/' + this.Token, data).then((response) => {
      console.log(response);
      this.response = response;
      if (this.response.Message == "success") {
        // if (this.closerDataArr.length > 0) {
        //   this.closerDataArr.splice(0, 1);
        // } else {
        if (i == this.closerDataArr.length - 1) {
          this.nativeStorage.remove('closerArr').then(
            data => {
              console.log(data)
            },
            error => console.error(error)
          );
        }
      } else {

      }
    });
  }
  /***************** closer sync work end********************* */
  /***************** closer doc sync work start********************* */
  offlineCloserDoc(ticketId, resp) {

    this.nativeStorage.getItem(ticketId + 'closerDoc')
      .then(
        data => {
          console.log(data)
          //  this.closerDataArr = data;
          for (let i = 0; i < data.length; i++) {
            this.syncAttachmentData(data[i], resp)
          }
        },
        error => console.error(error)
      );

    this.nativeStorage.remove(ticketId + 'closerDoc').then(
      data => {
        console.log(data)
      },
      error => console.error(error)
    );
  }

  syncAttachmentData(data, resp) {
    data.CloserID = resp.result.CloserID
    var Token = JSON.parse(localStorage.userData).Token;
    this.RestService.postServicesOffline('app/EngineerTickets/PostCloserAttachment/' + Token, data).then(async (response) => {
      console.log(response);
      if (this.response.Message == "success") {
        console.log('doc uploaded succesfully');
      } else {
      }
    });
  }
  /***************** closer doc sync work end********************* */
  /***************** assigne form sync work start********************* */
  synchAssignedForm() {
    this.nativeStorage.getItem('AssignForm')
      .then(
        data => {
          console.log(data)
          if (data.length > 0) {
            this.assignedFormArr = data;
            for (let i = 0; i < data.length; i++) {
              this.syncassignForm(data[i], i);
            }
          }
        },
        error => console.error(error)
      );
  }

  syncassignForm(item, i) {

    this.RestService.postServicesOffline('app/EngineerTickets/asignServiceformToTicket/' + this.Token, item).then((response) => {
      console.log(response);
      this.response = response;
      if (this.response.Message == "success") {
        //this.successAlert();
        if (i == this.assignedFormArr.length - 1) {
          this.nativeStorage.remove('AssignForm').then(
            data => {
              console.log(data)
            },
            error => console.error(error)
          );
        }

      } else {

      }
    });
  }
  /***************** assigne form sync work end********************* */


  async successAlert() {
    const alert = await this.alertController.create({
      header: '',
      message: 'Sync sucessfull',
      cssClass: 'secondary',
      buttons: [
        {
          text: 'Ok',
          role: 'ok',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
          }
        }
      ]
    });

    await alert.present();
  }


}
